/* 2020.10 ucompanion front-end team */

.mylearning-new {
  position: relative;

  &::before {
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    width: rem(380px);
    height: 100%;
    background: #5a5f68;
    content: "";
  }

  & + .quick .q-group {
    position: fixed;
    right: rem(10px);
    bottom: rem(100px);
  }
}

.aside-learning {
  float: left;
  overflow: hidden;
  width: rem(380px);
  background: #4b4d52;

  .aside-learning-title {
    position: relative;
    padding: rem(25px 25px 20px 25px);
    background: #4b4d52;

    p {
      margin-bottom: rem(12px);
      strong {
        color: #fff;
        font-size: 18px;
        word-wrap: break-word;
        word-break: break-all;
        text-decoration: underline;
      }
    }
    a {
      position: relative;
      display: inline-block;
      height: rem(20px);
      padding-right: rem(26px);
      padding-left: rem(17px);
      background: url("../img/icon-arrow-open-16-px.svg") 100% 50% no-repeat;
      background-size: 16px auto;
      color: #fff;
      font-size: 14px;
      line-height: rem(16px);
      font-weight: 200;

      &::before {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        display: block;
        width: 10px;
        height: 16px;
        background: url("../img/icon-lms-course.svg") 0 0 no-repeat;
        background-size: 10px auto;
        content: "";
      }
    }

    button {
      display: block;
      position: absolute;
      right: 5%;
      bottom: 15%;
      width: rem(24px);
      height: rem(24px);
      background: url("../img/icon-lms-learing-play.svg") 0 0 no-repeat;
      background-size: 24px auto;
    }
  }

  .aside-learning-list {
    background: #52565c;

    ul {
      clear: both;
      overflow: hidden;
      padding: rem(20px 20px 20px 25px);
      border-bottom: 1px solid #4b4d52;

      li {
        clear: both;
        overflow: hidden;
        position: relative;
        padding-top: rem(30px);

        a {
          position: relative;
          display: block;
          padding-left: rem(32px);
          color: #fff;
          font-size: 16px;

          &::before {
            position: absolute;
            left: 0;
            top: 40%;
            z-index: 1;
            width: rem(18px);
            height: rem(18px);
            margin-top: -rem(9px);
            border: 3px solid #fff;
            border-radius: 50%;
            content: "";
          }

          strong {
            display: block;
          }
        }

        p {
          padding-left: rem(32px);
          span {
            float: left;
            margin-right: rem(10px);
            color: #999;
            font-size: 12px;

            &:last-child {
              position: relative;
              padding: rem(0 0 0 10px);

              &::before {
                position: absolute;
                left: 0;
                top: 45%;
                width: 1px;
                height: 6px;
                background: rgba(185, 185, 185, 0.6);
                content: "";
              }
            }
          }
        }

        &:first-child {
          padding-top: 0;
        }
      }
    }
  }
}

.mylearning-new-contents {
  .main-info-learing {
    .clear-btn {
      position: absolute;
      top: rem(27px);
      right: rem(27px);
      width: rem(24px);
      height: rem(24px);
      cursor: pointer;
    }
    .main-info-learing-text {
      .learning-status {
        padding: rem(0 0 8px);
        font-family: NotoSansCJKkr-Regular;
        font-size: rem(16px);
        color: #666666;
      }
      .learning-title {
        padding: rem(0 0 21px);
        font-family: NotoSansCJKkr;
        font-size: rem(18px);
        font-weight: bold;
        letter-spacing: rem(-0.4px);
        text-decoration: underline;
      }
      .learning-button {
        width: rem(160px);
        height: rem(40px);
        border-radius: rem(4px);
        background-color: #ff664d;
        color: #ffff;
        font-family: NotoSansCJKkr-Medium;
        font-size: rem(14px);
      }
      text-align: center;
      padding: rem(30px 0);
    }
    position: relative;
    width: rem(920px);
    height: rem(180px);
    margin: rem(0 auto 50px);
    border: solid 1px #6c788f;
    background-color: #ffffff;
    border-radius: rem(8px);
    box-shadow: 0 3px 5px -3px #c9d3e6;
  }

  float: none;
  overflow: hidden;
  padding: rem(30px 50px);

  .main-info-area {
    background: 0;

    .main-info-wrap {
      display: block;
      width: 100%;
      padding: 0;
      background: 0;

      .thumbnail {
        left: calc(100% - 75px);
        top: rem(10px);
        width: rem(70px);
        height: rem(70px);
        background: url("../img/DT_2.png") no-repeat 0 0;
      }
    }

    .title-area {
      display: block;
      width: 100%;
      height: auto;
      margin: rem(0 0 50px 0);
      clear: both;
      overflow: hidden;

      .ui.blue.label {
        float: left;
        margin-right: rem(10px);
      }
      .ui.blue.label,
      .ui.blue.labels .label {
        background: #4b5dcc !important;
      }

      .header {
        display: block;
        float: none;
        overflow: hidden;
        width: auto;
        font-size: rem(20px);
        line-height: rem(24px);
        letter-spacing: rem(-0.2px);
        font-weight: 700;
      }

      .level-wrap {
        float: left;
      }

      .fixed-rating {
        clear: none;
        overflow: hidden;
        float: left;

        strong {
          font-weight: 700;
          font-size: 14px;
        }
      }

      .level-wrap {
        margin-top: rem(10px);
        padding-top: rem(0);
        .ui {
          &.level {
            float: left;
            height: rem(10px);
            margin: rem(4px 7px 0 0) !important;

            .step {
              float: left;
              width: rem(5px) !important;
              height: rem(10px);
              margin: rem(0);
              border-width: 1px 0 1px 1px;
              border-style: solid;
              border-color: #99acc4;
              &:nth-child(1) {
                width: rem(5px) !important;
                background-color: #f4f7fd;
                &::after {
                  background-color: #f4f7fd;
                  left: 0;
                }
              }
              &:nth-child(2) {
                background-color: #f4f7fd;
                &::after {
                  background-color: #f4f7fd;
                  left: rem(0);
                }
              }
              &:nth-child(3) {
                background-color: #f4f7fd;
                &::after {
                  background-color: #f4f7fd;
                  left: rem(0);
                }
              }
              &:nth-child(4) {
                background-color: #f4f7fd;
                margin-right: 0;
                border-width: 1px;
                &::after {
                  background-color: #f4f7fd;
                  right: 0;
                }
              }
              &.active {
                border-color: #23518a;
                background: #c9e1fe;
                &::after {
                  display: none;
                }
              }

              &.active + .step {
                border-left: 1px solid #23518a;
              }
            }
          }
        }
        .lv1 {
          position: absolute;
          left: 0;
          top: rem(20px);
          font-size: rem(12px);
          line-height: rem(18px);
          color: #4c4c4c;
        }
        .lv4 {
          position: absolute;
          right: 0;
          top: rem(20px);
          font-size: rem(12px);
          line-height: rem(18px);
          color: #4c4c4c;
        }
        .level-txt {
          float: left;
          width: auto;
          position: static;
          text-align: left;
          font-size: rem(14px);
          line-height: rem(16px);
          color: #444;
          font-weight: 700;

          &.step1 {
            text-indent: rem(0);
          }
          &.step2 {
            text-indent: rem(0);
          }
          &.step3 {
            text-indent: rem(0);
          }
          &.step4 {
            text-indent: rem(0);
          }
        }
      }

      ul.class-info {
        float: none;
        overflow: hidden;
        margin: rem(0 0 30px 0);
        padding: rem(10px 0 0 15px);

        li {
          float: left;
          position: relative;
          margin: 0;
          padding: rem(0 10px);

          &:first-child {
            padding-left: 0;

            &::before {
              display: none;
            }
          }

          &.time {
            .label {
              width: rem(16px);
              height: rem(18px);
              margin-right: rem(5px);
              background: url("../img/icon-card-time-16-px.svg") no-repeat 0 50%;
              background-size: 16px auto;
              font-size: 0;
            }
          }
          &.badge {
            .label {
              width: rem(16px);
              height: rem(18px);
              margin-right: rem(5px);
              background: url("../img/icon-learningtype-badge-16-px.svg")
                no-repeat 0 50%;
              background-size: 16px auto;
              font-size: 0;
            }
          }

          .btn-inquiry {
            width: rem(82px);
            height: rem(22px);
            margin-left: rem(15px);
            padding: rem(0 11px);
            border: 1px solid #6c788f;
            border-radius: 4px;
            background: 0;
            em {
              display: inline-block;
              height: rem(20px);
              padding-left: rem(16px);
              background: url("../img/icon-ask-11-px.svg") no-repeat 0 50%;
              background-size: 11px auto;
              line-height: rem(20px);
              letter-spacing: rem(-0.5px);
              color: #6c788f;
              font-size: 12px;
            }
          }

          &::before {
            position: absolute;
            left: 0;
            top: 40%;
            width: 1px;
            height: 6px;
            margin-left: 0;
            background: #d8d8d8;
            content: "";
          }

          .label {
            float: left;
            margin-right: rem(10px);
            color: #666;
            font-size: 14px;
          }

          .value {
            float: left;
            color: #444;
            font-size: 14px;
            font-weight: 700;
          }
        }
      }

      .foot-buttons {
        float: right;
        margin-top: rem(8px);

        .ui.button.img-icon {
          margin-left: rem(20px);

          .icon.bookmark2 {
            width: rem(18px);
            height: rem(18px);
            background: url("../img/btn-lms-bookmark-14-px.svg") no-repeat 0 50%;
            background-size: 14px auto;
          }
          .icon.share2 {
            width: rem(18px);
            height: rem(18px);
            background: url("../img/btn-lms-add-14-px.svg") no-repeat 0 50%;
            background-size: 14px auto;
          }
          span {
            color: #5b6477;
            font-size: rem(14px);
            font-weight: 400;
          }
        }
      }
    }
  }

  .contents {
    &.overview {
      width: 100%;
      padding-top: 0;

      .class-guide-txt {
        padding-top: rem(51px);
      }
      .title-style .onlytext span {
        font-size: rem(16px);
      }
      .course-cont.pre-course {
        .course-box {
          margin-top: rem(10px);

          .bar .tit span:first-child {
            display: inline-block;
            width: rem(40px);
            height: rem(22px);
            margin-right: rem(10px);
            text-align: center;
            border-radius: rem(4px);
            background: #5a5f68;
            color: #fff;
            font-size: rem(12px);
            line-height: rem(22px);
            font-weight: 700;

            &.red {
              background: #ea6950;
              color: #fff;
            }
            &.blue {
              background: #0e73db;
            }
          }
        }
      }
      .teacher-info {
        border-bottom: 1px solid #d6deed;
        padding: rem(52px 0 54px);
        .item {
          display: block;
          white-space: nowrap;

          .title .title-style {
            margin-bottom: rem(22px);
          }
          .detail {
            .teacher {
              display: inline-block;
              padding: rem(0 30px 24px 0);
              cursor: pointer;

              .hover-content {
                display: none;
              }

              //hover text box
              &:hover {
                .hover-content {
                  z-index: 1;
                  display: block;
                  position: absolute;
                  top: rem(150px);
                  width: rem(78px);
                  height: rem(79px);
                  border-radius: rem(2px);
                  border: solid 2px #6c788f;
                  background-color: #ffffff;
                  &::after {
                    border-top: 0px solid #6c788f;
                    border-left: 5px solid #6c788f;
                    border-right: 5px solid transparent;
                    border-bottom: 10px solid #ffffff;
                    content: "";
                    position: absolute;
                    top: rem(-10px);
                    left: 50%;
                    transform: translateX(-50%);
                  }
                }
              }

              &:nth-child(1) {
                padding: rem(0 30px 24px 0);
              }

              img {
                width: rem(80px);
                height: rem(80px);
                border-radius: rem(50%);
              }
            }
          }
        }
        .scroll-bar {
          position: relative;
          width: rem(920px);
          height: rem(4px);
          margin-top: rem(24px);
          border-radius: rem(2px);
          background-color: #c9d3e6;

          .scroll-button {
            position: absolute;
            top: 0;
            left: 5%;
            width: rem(60px);
            height: rem(12px);
            margin: rem(-5px 0);
            border-radius: rem(6px);
            box-shadow: 0 2px 6px 0 #ccd4e7;
            background-color: #ffffff;
            cursor: pointer;
          }
        }
      }
      .relation-badge-area {
        padding: rem(52px 0);
        border-bottom: 1px solid #d6deed;
        background-color: #f4f7fd;

        .segment {
          margin: 0;

          .title-style {
            margin-bottom: rem(22px);
          }

          .list ul li {
            margin-right: rem(40px);
          }
        }

        .badge.s220 .title .cell span {
          line-height: 3rem;
        }
      }

      .relation-course {
        padding: rem(52px 0);
        .segment {
          margin: 0;

          .title-style {
            margin-bottom: rem(22px);
          }
        }

        .scroll-bar {
          position: relative;
          width: rem(920px);
          height: rem(4px);
          margin-top: rem(24px);
          border-radius: rem(2px);
          background-color: #c9d3e6;

          .scroll-button {
            position: absolute;
            top: 0;
            left: 5%;
            width: rem(60px);
            height: rem(12px);
            margin: rem(-5px 0);
            border-radius: rem(6px);
            box-shadow: 0 2px 6px 0 #ccd4e7;
            background-color: #ffffff;
            cursor: pointer;
          }
        }
      }
    }

    .tab-menu {
      position: relative;

      .count {
        color: #e1002a !important;
      }

      &::before {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: rem(1px);
        background: #d6deed;
        content: "";
      }
      .cont-inner {
        width: 100%;
      }
    }
  }
}
