/*권은별*/

/* My learning - card */
.ui {
  &.cards {
    margin: 0;
    clear: both;
    & + .more-comments {
      padding-top: rem(17px);
    }
  }
  &.box-cards {
    margin-top: rem(-13px);
    .card {
      position: relative;
      padding: rem(28px 30px 30px);
      width: rem(280px);
      height: rem(360px);
      box-sizing: border-box;
      box-shadow: 0 2px 6px 0 #ccd4e7;
      background: #fff;
      margin: rem(13px);
      &:nth-child(1),
      &:nth-child(4n + 1) {
        margin-left: 0;
      }
      &:nth-child(4n) {
        margin-right: 0;
      }
      .card-inner {
        .title-area {
          margin-bottom: rem(15px);
          .label.color {
            margin-bottom: rem(10px);
            min-width: rem(40px);
            text-align: center;
            padding: rem(0 13px) !important;
          }
          .header {
            max-height: rem(84px);
            width: rem(220px);
            height: rem(86px);
            background: none;
            font-size: rem(20px);
            color: #222;
            font-weight: 700;
            letter-spacing: -0.5px;
            line-height: rem(28px);
            white-space: normal;
            word-wrap: break-word;
            display: block;
            display: -webkit-box;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical;
            overflow: hidden;
          }
        }
        .icon-area {
          .li {
            overflow: hidden;
            height: rem(24px);
            margin-bottom: rem(4px);
            .label {
              display: inline-block;
            }
            .g-text {
              position: relative;
              font-size: rem(14px);
              line-height: rem(24px);
              color: #4c4c4c;
              padding-left: rem(16px);
              vertical-align: middle;
              &::after {
                position: absolute;
                left: rem(5px);
                top: 50%;
                margin-top: rem(-4px);
                content: "";
                display: block;
                width: rem(1px);
                height: rem(10px);
                background: #b9b9b9;
              }
              &.size12 {
                font-size: rem(12px);
                &::after {
                  margin-top: rem(-4px);
                }
              }
            }
            .card-stamp {
              margin-left: rem(20px);
            }
          }
        }
        .foot-area {
          position: absolute;
          bottom: rem(33px);
          .study-date {
            margin-top: rem(8px);
            font-size: rem(12px);
            color: #666;
            line-height: rem(18px);
          }
        }
      }
      .card-ribbon-wrap {
        position: absolute;
        top: rem(-8px);
        right: rem(30px);
        .label {
          &:nth-child(2) {
            display: none;
          }
        }
      }
      .hover-content {
        display: none;
        .title-area {
          margin-bottom: rem(8px);
          .label {
            margin-bottom: rem(10px);
            min-width: rem(40px);
            text-align: center;
          }
          .header {
            max-height: rem(84px);
            width: rem(220px);
            height: auto;
            background: none;
            font-size: rem(20px);
            color: #fff;
            font-weight: 700;
            letter-spacing: -0.5px;
            line-height: rem(28px);
            white-space: normal;
            word-wrap: break-word;
            display: block;
            display: -webkit-box;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical;
            overflow: hidden;
          }
        }
        .text-area {
          max-height: rem(96px);
          font-size: rem(14px);
          line-height: rem(24px);
          letter-spacing: rem(-0.35px);
          color: #fff;
          line-height: rem(24px);
          white-space: normal;
          word-wrap: break-word;
          display: block;
          display: -webkit-box;
          -webkit-line-clamp: 4;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
        .btn-area {
          position: absolute;
          bottom: rem(30px);
          button:nth-child(1) {
            margin-right: rem(6px);
          }
          .fix {
            width: rem(158px);
            font-size: rem(14px);
          }
        }
      }
      .thumbnail {
        position: absolute;
        bottom: rem(30px);
        right: rem(30px);
        width: rem(60px);
        height: rem(60px);
        border-radius: rem(4px);
        background: url("../img/thumb-card-60-px.jpg") 0 0 no-repeat;
        overflow: hidden;
        &.completed {
          background: url("../img/thumb-card-complete-60-px@2x.png") 0 0
            no-repeat;
          background-size: 60px 60px;
        }
        img {
          width: 100%;
          height: 100%;
        }
        i {
          width: rem(60px);
          height: rem(60px);
          &::before {
            width: rem(60px);
            height: rem(60px);
            background-size: 100% auto !important;
          }
        }
      }
      &.on {
        background: #32353b;
        .card-inner {
          display: none;
        }
        .hover-content {
          display: block;
          .label.color {
            padding: rem(0 13px);
          }
        }
      }
    }
  }

  &.list-cards .card {
    position: relative;
    width: 100%;
    height: rem(177px);
    padding: rem(30px 30px 25px 116px);
    box-sizing: border-box;
    background: #fff;
    box-shadow: 0 2px 6px 0 #ccd4e7;
    margin: rem(0 0 31px);
    &:last-child {
      margin-bottom: 0;
    }
    &::after {
      content: "";
      display: block;
      clear: both;
    }
    .card-ribbon-wrap {
      position: absolute;
      top: rem(-8px);
      right: rem(30px);
    }
    .thumbnail {
      position: absolute;
      top: rem(30px);
      left: rem(30px);
      width: rem(60px);
      height: rem(60px);
      border-radius: rem(4px);
      background: url("../img/thumb-card-60-px.jpg") 0 0 no-repeat;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
      }
      &.completed {
        background: url("../img/thumb-card-complete-60-px@2x.png") 0 0 no-repeat;
        background-size: 60px 60px;
        a {
          display: block;
          width: 100%;
          height: 100%;
        }
      }
    }
    .title-area {
      position: relative;
      float: left;
      width: rem(581px);
      margin-top: rem(-2px);
      .label.color {
        margin-bottom: rem(6px);
      }
      .header {
        a {
          margin-bottom: rem(10px);
          width: rem(454px);
          height: rem(58px);
          font-size: rem(20px);
          color: #222;
          line-height: rem(28px);
          font-weight: 700;
          white-space: normal;
          word-wrap: break-word;
          display: block;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
      }
      &::after {
        position: absolute;
        right: 0;
        top: rem(29px);
        content: "";
        width: 1px;
        height: rem(60px);
        background: #eee;
      }
    }
    .btn-area {
      position: absolute;
      top: rem(55px);
      left: rem(757px);
      .fix {
        width: rem(158px);
        height: rem(40px);
      }
    }
    .time-area {
      float: right;
      width: rem(290px);
      text-align: right;
      padding-top: rem(24px);
      .time {
        margin-top: rem(1px);
        vertical-align: bottom;
        strong {
          font-weight: 700;
          font-size: rem(32px);
          color: #222;
          &.ml9 {
            margin-left: rem(9px);
          }
        }
        span {
          font-weight: 700;
          font-size: rem(20px);
          color: #333;
        }
      }
      .location {
        .location-name {
          vertical-align: middle;
          font-size: rem(14px);
          color: #4c4c4c;
          & + .label {
            position: relative;
            padding-left: rem(13px);
            &:after {
              position: absolute;
              left: 5px;
              top: 50%;
              margin-top: rem(-3px);
              content: "";
              width: rem(1px);
              height: rem(8px);
              background: #b9b9b9;
            }
          }
        }
      }
      .stamp {
        font-weight: 700;
        color: #222;
        line-height: rem(28px);
        font-size: rem(20px);
        margin-top: rem(10px);
        strong {
          margin-left: rem(4px);
          font-size: rem(32px);
          font-weight: 700;
        }
      }
    }
  }
  &.expert-cards {
    margin-top: rem(-13px);
  }
  &.expert-cards .card {
    position: relative;
    padding: rem(30px);
    width: rem(280px);
    height: rem(200px);
    box-sizing: border-box;
    box-shadow: rem(0 2px 6px 0) #ccd4e7;
    background: #fff;
    margin: rem(13px);
    &:nth-child(1),
    &:nth-child(4n + 1) {
      margin-left: 0;
    }
    &:nth-child(4n) {
      margin-right: 0;
    }
    .ui.profile {
      position: absolute;
      right: rem(30px);
      top: rem(30px);
    }
    .name {
      display: block;
      margin: rem(0 85px 10px 0);
      font-size: rem(20px);
      font-weight: 700;
      line-height: 1.4;
      letter-spacing: rem(-0.5px);
      color: #222;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .co {
      display: block;
      margin: rem(0 85px 2px 0);
      font-size: rem(14px);
      letter-spacing: rem(-0.2px);
      color: #4c4c4c;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .instructor {
      display: block;
      margin: rem(0 85px 2px 0);
      font-size: rem(14px);
      letter-spacing: rem(-0.2px);
      color: #4c4c4c;
    }
    .channel {
      position: absolute;
      left: rem(30px);
      bottom: rem(30px);
      font-size: rem(14px);
      font-weight: 700;
      letter-spacing: rem(-0.2px);
      color: #222;
      i {
        margin: rem(0 8px 0 0);
        position: relative;
        top: rem(2px);
      }
      span {
        text-overflow: ellipsis;
        white-space: nowrap;
        word-wrap: normal;
        overflow: hidden;
        display: inline-block;
        width: rem(196px);
      }
    }
    a.history {
      display: block;
    }
  }
}

/*link style blue text*/
.link-style {
  color: #0e73db;
  text-decoration: underline;
  &:hover {
    color: #0e73db;
    text-decoration: underline;
  }
}

/*고정된 별점값, 기능 無, 기본 16px .pop은 24px*/
.fixed-rating {
  span {
    display: inline-block;
    width: rem(16px);
    height: rem(16px);
    margin-right: rem(4px);
    background: url("../img/icon-rate-nomal-16-px.svg") 0 0 no-repeat;
    background-size: 100% auto;
    &:last-child {
      margin-right: 0 !important;
    }
  }
  &.pop {
    span {
      width: rem(24px);
      height: rem(24px);
      margin-right: rem(6px);
    }
  }
  &.line {
    span {
      background: url("../img/icon-rate-line-24-px.svg") 0 0 no-repeat;
      background-size: 100% auto;
    }
  }
  &.s1 {
    span {
      &:nth-child(1) {
        background-image: url("../img/icon-rate-active-16-px.svg");
      }
    }
  }
  &.s2 {
    span {
      &:nth-child(-n + 2) {
        background-image: url("../img/icon-rate-active-16-px.svg");
      }
    }
  }
  &.s3 {
    span {
      &:nth-child(-n + 3) {
        background-image: url("../img/icon-rate-active-16-px.svg");
      }
    }
  }
  &.s4 {
    span {
      &:nth-child(-n + 4) {
        background-image: url("../img/icon-rate-active-16-px.svg");
      }
    }
  }
  &.s5 {
    span {
      &:nth-child(-n + 5) {
        background-image: url("../img/icon-rate-active-16-px.svg");
      }
    }
  }
}

/*main-info-area*/
.main-info-area {
  width: 100%;
  background: #fff;
  /* content-division-t1 : RC-01-01, RC-01-02, RC-02-01, RC-02-02 */
  &.content-division {
    border-bottom: rem(1px) solid #d6deed;
  }
  .main-info-wrap {
    position: relative;
    display: table;
    table-layout: fixed;
    width: rem(1200px);
    padding: rem(40px 0 38px 90px);
    //height:rem(262px);
    margin: 0 auto;
    //padding-left:rem(100px);
    background: #fff;
    .thumbnail {
      position: absolute;
      top: rem(40px);
      left: rem(0);
      background: url("../img/thumb-info-70-px.jpg") 0 0 no-repeat;
      width: rem(70px);
      height: rem(70px);
      overflow: hidden;
      border-radius: rem(4px);
      img {
        width: 100%;
        height: 100%;
      }
    }
    .title-area {
      position: relative;
      display: table-cell;
      width: rem(790px);
      height: 100%;
      box-sizing: border-box;
      vertical-align: top;
      .header {
        width: rem(686px);
        font-size: rem(30px);
        line-height: rem(42px);
        letter-spacing: rem(-0.8px);
        color: #222;
        word-break: normal;
        word-wrap: break-word;
      }
      .deatil {
        /*
                position:absolute;
                left:0;
                bottom:rem(36px);
                */
        margin-top: rem(10px);
        .item {
          .onlytext {
            margin: 0;
            vertical-align: top;
            span {
              line-height: rem(24px);
              vertical-align: top;
            }
            .icon {
              margin-top: rem(4px);
            }
          }
          .channel {
            position: relative;
            padding-left: rem(11px);
            margin-left: rem(6px);
            font-size: rem(14px);
            line-height: rem(24px);
            color: #4c4c4c;
            vertical-align: top;
            &::before {
              content: "";
              position: absolute;
              left: 0;
              top: 12px;
              margin-top: rem(-6px);
              background: #b9b9b9;
              width: rem(1px);
              height: rem(10px);
              vertical-align: top;
            }
          }
          .ml17 {
            margin-left: rem(17px);
          }
        }
      }
    }
    .right-area {
      display: table-cell;
      width: rem(310px);
      text-align: center;
      vertical-align: middle;
      .price {
        display: inline-block;
        margin-top: rem(16px);
        margin-bottom: rem(14px);
        font-size: rem(32px);
        line-height: rem(42px);
        letter-spacing: rem(-0.5px);
        font-weight: 700;
        color: #222;
        padding-left: rem(36px);
        background: url("../img/icon-price-32-px.svg") 0 5px no-repeat;
      }
      .fixed-rating {
        span {
          width: rem(24px);
          height: rem(24px);
          margin-right: rem(6px);
        }
      }
      .ui.stamp2 {
        & + .fixed-rating {
          padding-top: 0;
        }
      }
      .ui.stamp2.label {
        margin: rem(0 0 7px 0);
      }
      .stamp {
        display: block;
        text-align: center;
        margin: -14px 0 12px 0;
        .value {
          display: inline-block;
          width: rem(58px);
          height: rem(59px);
          text-align: center;
          line-height: rem(59px);
          font-size: rem(26px) !important;
          color: #ff664d;
          font-weight: 700;
          background: url("../img/badge-summary-stamp-2.svg") 0 0 no-repeat;
        }
        .label {
          margin-top: rem(3px);
          font-size: rem(12px);
          line-height: rem(18px);
          font-weight: 700;
        }
      }
    }
  }
  .progress-info-wrap {
    position: relative;
    display: table;
    table-layout: fixed;
    width: rem(1200px);
    padding: rem(60px 0 40px);
    margin: 0 auto;
    background: #fff;
    // overflow: hidden;
    .cell {
      display: table-cell;
      vertical-align: middle;
      // overflow: hidden;
      &:nth-child(1) {
        width: rem(412px);
        padding-top: rem(2px);
        .cell-inner {
          position: relative;
          &::after {
            position: absolute;
            right: 0;
            top: rem(11px);
            content: "";
            display: inline-block;
            width: rem(1px);
            height: rem(40px);
            background: #eee;
          }
        }
      }
      &:nth-child(2) {
        position: relative;
        padding: rem(0 0 0 62px);
        vertical-align: top;
        .cell-inner {
          position: relative;
          &::after {
            position: absolute;
            right: 0;
            top: rem(11px);
            content: "";
            display: inline-block;
            width: rem(1px);
            height: rem(40px);
            background: #eee;
          }
        }
        &:last-of-type {
          .cell-inner {
            &::after {
              display: none;
            }
          }
        }
      }
      &:nth-child(3) {
        width: rem(248px);
        vertical-align: top;
        text-align: left;
        padding-left: rem(60px);
      }

      .profile {
        position: relative;
        display: inline-block;
        width: rem(56px);
        height: rem(56px);

        vertical-align: top;
        .pic {
          width: 100%;
          height: 100%;
          border: rem(1px) solid #ddd;
          box-sizing: border-box;
          border-radius: 100%;
          background: url("../img/img-profile-56-px.png") 0 0 no-repeat;
          background-size: 100% auto;
          overflow: hidden;
          img {
            width: 100%;
            height: 100%;
          }
        }
        button {
          position: absolute;
          bottom: 0;
          right: 0;
        }
      }
      .text-info {
        display: inline-block;
        margin-left: rem(16px);
        .name {
          margin-bottom: rem(8px);
          font-size: rem(28px);
          color: #222;
          letter-spacing: rem(-0.5px);
          display: inline-block;
          line-height: rem(30px);
          max-width: rem(280px);
          max-height: rem(60px);
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: normal;
          word-break: normal;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        .part {
          position: relative;
          top: rem(-2px);
          font-size: rem(14px);
          line-height: rem(17px);
          color: #6c788f;
          /*
                    span:nth-child(1){
                        position:relative;
                        display:inline-block;
                        padding-right:rem(8px);
                        margin-right:rem(8px);
                        &::after{
                            content:'';
                            position:absolute;
                            right:0;
                            top:50%;
                            margin-top:rem(-4px);
                            width:rem(1px);
                            height:rem(10px);
                            background:#c8d0e0;
                        }
                    }*/
        }
      }
      .total-time {
        display: inline-block;
        //width:rem(178px); Mypage 상단 원래버전
        text-align: left;
        margin-bottom: 0;
        padding-bottom: 0;
      }
      .chart-wrap {
        display: inline-block;
        overflow: hidden;
        vertical-align: middle;
        .pie {
          float: left;
        }
        .list {
          float: left;
          margin: rem(9px 0 0 10px);
          padding: 0;
          .item {
            padding: 0;
            margin: rem(0 0 8px 0);
            &::after {
              display: none;
            }
            dt {
              position: relative;
              display: inline-block;
              padding-left: rem(10px);
              width: rem(107px);
              font-size: rem(14px);
              color: #666;
              line-height: 1;
              &::after {
                position: absolute;
                left: 0;
                top: rem(4px);
                content: "";
                display: block;
                width: rem(6px);
                height: rem(6px);
                border-radius: 100%;
              }
            }
            dd {
              display: inline-block;
              font-size: rem(16px);
              line-height: 1;
            }
            &.sk {
              dt {
                &::after {
                  background: #ff664d;
                }
              }
            }
            &.my {
              margin-bottom: 0;
              dt {
                &::after {
                  background: #545d8b;
                }
              }
            }
          }
        }
      }
      .stamp-wrap {
        position: relative;
        height: rem(61px);
        .year {
          position: absolute;
          right: 0;
          top: rem(-39px);
        }
      }
      .wating {
        display: inline-block;
        vertical-align: middle;
        .blue-arrow2 {
          display: block;
          margin-bottom: rem(8px);
          font-size: rem(14px);
          line-height: rem(16px);
        }
        strong {
          font-size: rem(24px);
          line-height: rem(34px);
          color: #333;
          font-weight: 700;
          vertical-align: bottom;
        }
      }
      .community-num {
        display: inline-block;
        width: rem(161px);
        text-align: left;
        margin-bottom: 0;
        margin-left: 0;
        padding-bottom: 0;
        .label {
          text-align: left;
          margin-bottom: rem(9px);
          .icon {
            margin-right: rem(2px);
          }
          span {
            font-size: rem(14px);
            line-height: rem(16px);
            color: #666;
            letter-spacing: rem(-0.2px);
          }
        }
        .value2 {
          strong {
            font-size: rem(32px);
            font-weight: 700;
            line-height: rem(34px);
            vertical-align: bottom;
            color: #222;
            &.min {
              margin-left: rem(6px);
            }
          }
          span {
            display: inline-block;
            margin: rem(2px 0 0 2px);
            font-size: rem(20px);
            font-weight: 700;
            line-height: rem(28px);
            vertical-align: bottom;
            color: #333;
          }
        }
      }
    }
  }
  .channel-of-interest {
    position: relative;
    top: rem(-8px);
    width: rem(1200px);
    margin: 0 auto;
    padding: rem(0 0 32px 0);
  }
}

.mypage {
  &.progress-info-wrap {
    .cell {
      &:nth-child(2) {
        padding: 0;
        .cell-inner {
          padding: rem(0 58px);
        }
      }
      &:nth-child(3) {
        width: rem(209px);
