      float: none;
      &::after {
        content: "";
        display: block;
        clear: both;
      }
      .step {
        float: left;
        width: rem(54px) !important;
        height: rem(6px);
        padding: 0;
        border-radius: 0;
        margin: 0 2px 0 0;
        border: 0;
        &::after {
          display: none;
        }
        &:nth-child(1) {
          width: rem(52px) !important;
          background-color: #e0eeff;
          &::after {
            background-color: #e0eeff;
            left: 0;
          }
        }
        &:nth-child(2) {
          background-color: #c9e1fe;
          &::after {
            background-color: #c9e1fe;
            left: rem(-7px);
          }
        }
        &:nth-child(3) {
          background-color: #95c1f8;
          &::after {
            background-color: #95c1f8;
            left: rem(-7px);
          }
        }
        &:nth-child(4) {
          background-color: #3e8ae9;
          margin-right: 0;
          &::after {
            background-color: #3e8ae9;
            right: 0;
          }
        }
        &.active {
          &::after {
            display: block;
            content: "";
            position: absolute;
            width: rem(68px) !important;
            height: rem(12px);
            top: rem(-3px);
            border: 1px solid #1b3b6c;
            box-sizing: border-box;
            box-shadow: 0 4px 5px 0 rgba(68, 99, 163, 0.36);
            z-index: 2;
            transform: none;
          }
        }
