      width: 1px;
      height: 1px;
      padding: 0;
      margin: -1px;
      overflow: hidden;
      clip: rect(0, 0, 0, 0);
      border: 0;

      & + label {
        display: inline-block;
        position: relative;
        padding: 0 0 0 rem(33px);
        cursor: pointer;
        user-select: none;
        color: rgba(102, 102, 102, 0.9);
        &:before {
          content: "";
          position: absolute;
          left: 0;
          top: 50%;
