          height: rem(24px);
          margin-top: rem(-11px);
          text-align: center;
          background: url("../img/btn-radio-type-2-nomal.svg") no-repeat 0 0;
        }
      }

      &:checked + label {
        &:before {
          background-image: url("../img/btn-radio-type-2-selected.svg");
        }
      }
    }
  }
}

