//progress bar (코스 리스트 play 버튼 ML-06-02 / ML-06-02-01)
.pie-wrapper {
  display: inline-block;
  height: rem(24px);
  width: rem(24px);
  position: relative;
  background: url("../img/icon_learing_ing.png") 50% 50% no-repeat;
  background-size: 100% auto;
  .pie {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    clip: rect(0, 24px, 24px, 12px);
    & > span {
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border: 2px solid #ff664d;
      border-radius: 50%;
      box-sizing: border-box;
      clip: rect(0, 12px, 24px, 0);
    }
  }
  .shadow {
    display: block;
    box-sizing: border-box;
    height: 100%;
    width: 100%;
    border: 2px solid #bdc3c7;
    border-radius: 50%;
  }
  &.progress-0 .pie .left-side {
    transform: rotate(0deg);
  }
  &.progress-0 .pie .right-side {
    display: none;
  }
  &.progress-1 .pie .left-side {
    transform: rotate(3.6deg);
  }
  &.progress-1 .pie .right-side {
    display: none;
  }
  &.progress-2 .pie .left-side {
