//ML-06-02 / ML-06-02-01 코스 상세화면
.course-cont {
  .btn-completed {
    display: table-cell;
    position: relative;
    width: rem(110px);
    padding: 1.1rem 0 1.1rem 0.8rem;
    margin-left: 1rem;
    &::before {
      position: absolute;
      left: 0;
      top: 50%;
      margin-top: rem(-6px);
      content: "";
      width: rem(1px);
      height: rem(14px);
      background: #b9b9b9;
    }

    & + .toggle-btn {
      padding-left: 0.8rem !important;
    }
    .text {
      display: inline-block;
      margin-right: rem(5px);
      font-size: rem(14px);
      vertical-align: middle;
      color: #6c788f;
      font-weight: 700;
      &.no-link {
        text-decoration: none;
      }
    }
    i.icon.play-completed24 {
      vertical-align: middle !important;
    }
  }

  .btn-play {
    display: inline-block;
    .text {
      display: inline-block;
      margin-right: rem(5px);
      font-size: rem(14px);
      vertical-align: middle;
      color: #6c788f;
      font-weight: 700;
      &.no-link {
        text-decoration: none;
      }
    }

    &.black {
      .text {
        color: #4c4c4c;
      }
    }
    &.orange {
      .text {
        color: #ff664d;
      }
    }
    &.completed {
      .text {
        color: #6c788f;
      }
    }
    &.disabled {
      cursor: default !important;
      .text {
        color: #d9d9d9;
      }
    }
    .pie-wrapper {
      display: inline-block;
      vertical-align: middle;
    }
    .icon {
      vertical-align: middle;
      margin: 0;
    }
  }
  .cube-box {
    border-radius: rem(8px);
    box-shadow: 0 1px 3px 0 #ccd4e7;
    background: #fff;
    .bar {
      display: table;
      width: 100%;
      height: rem(64px);
      &.typeA {
        .tit {
          display: table-cell;
          vertical-align: middle;
          padding: rem(0 20px);
          width: rem(480px);
          a {
            max-width: rem(460px);
            font-size: rem(16px);
            color: #222;
            letter-spacing: rem(-0.4px);
            font-weight: 700;
            text-decoration: underline;
          }
        }
        .right {
          display: table-cell;
          vertical-align: middle;
          text-align: right;
          padding: rem(0 20px);
          width: rem(320px);
          & > span {
            display: inline-block;
            position: relative;
            font-size: 14px;
