    border-radius: rem(0 0 4px 4px);
    border: rem(1px) solid #979797;
    background: #f5f5f5;
    table {
      min-height: rem(86px);
      tr {
        th {
          width: rem(176px);
          padding-left: rem(30px);
          vertical-align: middle;
          text-align: left;
          .clear + span {
            padding-left: rem(6px);
            font-size: rem(14px);
            color: #222;
            letter-spacing: rem(-0.2px);
            vertical-align: middle;
          }
        }
        td {
          padding: rem(24px 0 14px);
          .del {
            margin: rem(0 10px 10px 0);
          }
        }
      }
    }
  }
  .calendar-cell {
    display: inline-block;
    margin-right: rem(28px);
    padding-bottom: rem(20px);
    > .calendar {
      display: inline-block;
      width: rem(240px);
      vertical-align: middle;
      + .dash {
        vertical-align: middle;
        margin: rem(0 14px);
      }
    }
  }
}
