body {
  &.white {
    background: #fff;
  }
  &.main {
    background: #f4f7fd;
    > .content {
      padding-bottom: rem(0);
    }
  }
}
/* semantic-ui override */
.ui {
  &.wait {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 999;
    display: none;
    &::before {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(255, 255, 255, 0.8);
    }
    div {
      position: absolute;
      left: 50%;
      top: 50%;
      margin: rem(-60px 0 0 -60px);
      display: block;
      width: rem(120px);
      font-size: rem(20px);
      font-weight: 700;
      color: #222;
      text-align: center;
      z-index: 2;
      &::before {
        content: "";
        display: block;
        width: rem(120px);
        height: rem(120px);
        background: url(../img/loading.png) no-repeat 0 0;
      }
    }
    &.s1 {
      div {
        &::before {
          background-position: rem(-120px 0);
        }
      }
    }
    &.s2 {
      div {
        &::before {
          background-position: rem(-240px 0);
        }
      }
    }
    &.s3 {
      div {
        &::before {
          background-position: rem(-360px 0);
        }
      }
    }
    &.s4 {
      div {
        &::before {
          background-position: rem(-480px 0);
        }
      }
    }
    &.s5 {
      div {
        &::before {
          background-position: rem(-600px 0);
        }
      }
    }
    &.s6 {
      div {
        &::before {
          background-position: rem(-720px 0);
        }
      }
    }
    &.s7 {
      div {
        &::before {
          background-position: rem(-840px 0);
        }
      }
    }
    &.s8 {
      div {
        &::before {
          background-position: rem(-960px 0);
        }
      }
    }
    &.s9 {
      div {
        &::before {
          background-position: rem(-1080px 0);
        }
      }
    }
    &.s10 {
      div {
        &::before {
          background-position: rem(-1200px 0);
        }
      }
    }
    &.s11 {
      div {
        &::before {
          background-position: rem(-1320px 0);
        }
      }
    }
    &.s12 {
      div {
        &::before {
          background-position: rem(-1440px 0);
        }
      }
    }
    &.s13 {
