  &.label {
    &.tag {
      display: inline-block;
      padding: rem(0 12px);
      font-size: rem(12px);
      line-height: rem(22px);
      color: #6c788f;
      font-weight: 400;
      background: #fff;
      border: 1px solid #d1d6e2;
      border-radius: rem(50px);
      &::before {
        display: none;
      }
      &::after {
        display: none;
      }
    }
    &.color {
      margin: 0;
      padding: rem(0 13px);
      font-size: rem(12px);
      line-height: rem(24px);
      font-weight: 700;
      &.navy {
        background-color: #01297a;
        border-color: #01297a;
        color: #fff;
      }
      &.marine {
        background-color: #4b6bcc;
        border-color: #4b6bcc;
        color: #fff;
      }
