        border-color: #009abd;
        color: #fff;
      }
    }
    &.stamp {
      background: transparent;
      padding: 0;
      margin: 0 auto;
      text-align: left;
      a {
        opacity: 1;
      }
      .text1 {
        display: block;
        margin: rem(0 0 8px 0);
        padding: rem(0 0 0 3px);
        font-size: rem(12px);
        font-weight: 400;
        line-height: 1.5;
        color: #666;
      }
      .text2 {
        margin: rem(0 2px);
        font-size: rem(20px);
        font-weight: 700;
        color: #ea644d;
      }
      .text3 {
        font-size: rem(32px);
        font-weight: 700;
        color: #ea644d;
      }
      .icon {
        position: relative;
        top: rem(-2px);
      }
    }
    &.stamp2 {
      background: transparent;
      padding: 0;
      margin: 0 auto;
      text-align: center;
      .text1 {
        margin: rem(0 0 0 6px);
        font-size: rem(28px);
        font-weight: 700;
        color: #ea644d;
      }
      .text2 {
        font-size: rem(36px);
        font-weight: 700;
        color: #ea644d;
      }
      .text3 {
        display: block;
        margin-top: rem(-4px);
        font-size: rem(12px);
        font-weight: 700;
        line-height: 1.5;
        color: #222;
      }
      .icon {
        position: relative;
        top: rem(-10px);
      }
    }
    &.ribbon2 {
      position: relative;
      min-width: rem(70px);
      box-sizing: border-box;
      padding: 0 5px;
      margin-left: rem(6px);
      color: #fff;
      background: #515a6b;
      box-shadow: rem(-2px 2px 0 0) rgba(0, 0, 0, 0.1);
      border-radius: 0;
      font-size: rem(12px);
      line-height: rem(32px);
      text-align: center;
      font-weight: 700;
      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: -6px;
        background-color: transparent !important;
        border-style: solid;
        border-width: 8px 6px 0 0;
        border-color: #232636;
        border-right-color: inherit;
        width: 0;
        height: 0;
        transform: rotate(180deg);
      }
    }
    &.user {
      position: relative;
      width: 100% !important;
      height: rem(38px);
      padding: rem(0 50px 0 0);
      top: rem(16px);
      background: transparent;
      text-align: right;
      img {
        position: absolute;
        right: 0;
        top: 0;
        width: rem(38px) !important;
        height: rem(38px) !important;
        margin: 0;
        border-radius: rem(100%);
        border: rem(1px) solid #ddd;
      }
      span {
        display: block;
        &.name {
          font-size: rem(12px);
          font-weight: 700;
          line-height: 1.33;
          color: #4c4c4c;
          display: inline-block;
          width: rem(125px);
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          text-decoration: underline;
        }
        &.affiliation {
          font-size: rem(12px);
          font-weight: 400;
          line-height: 1.33;
          color: #6c788f;
          display: inline-block;
          width: rem(125px);
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
