/* layout */
body {
  background: #f4f7fd;
  min-width: rem(1240px);
  overflow-x: auto;
  .cont-inner {
    position: relative;
    width: rem(1200px);
    margin: 0 auto;
  }
  > .header {
    width: 100%;
    min-width: rem(1240px);
    .group {
      position: relative;
      height: rem(76px);
      background: #fff;
      .cont-inner {
        height: 100%;
      }
      .g-logo {
        position: absolute;
        padding-top: rem(26px);
        box-sizing: border-box;
        width: rem(98px);
        height: 100%;
        i {
          vertical-align: top;
        }
      }
      .g-menu-detail {
        position: absolute;
        left: rem(131px);
        top: rem(0);
        &::before {
          content: "";
          width: rem(1px);
          height: rem(76px);
          background: #eee;
          position: absolute;
          top: rem(0);
          left: rem(152px);
        }
        .detail-open {
          position: absolute;
          left: 0;
          top: rem(25px);
          padding: rem(0 23px 0 45px);
          height: rem(32px);
          font-size: rem(12px);
          font-weight: 700;
          color: #6c788f;
          background: #fff url(../img/icon-category-gray-16-px.svg) no-repeat
            rem(23px 50%);
          border-radius: rem(4px);
          border: 1px solid #6c788f;
        }
        .layer {
          display: none;
          position: absolute;
          z-index: 10;
          left: 0;
          top: rem(60px);
          width: rem(527px);
          // height:rem(337px);
          background: #fff;
          border-radius: rem(4px);
          box-shadow: rem(0 1px 7px -3px) #3a4f82;
          .table-css {
            width: 100%;
            .row {
              &.head {
                .cell {
                  width: rem(160px);
                  border-bottom: rem(1px) solid #d6deed;
                  font-size: rem(12px);
                  font-weight: 700;
                  color: #222;
                  text-indent: rem(20px);
                  height: rem(39px);
                  &:last-of-type {
                    width: auto;
                    border-left: rem(1px) solid #d6deed;
                  }
                }
              }
              &.body {
                .cell {
                  height: rem(370px);
                  &:first-of-type {
                    .select-area {
                      width: 100%;
                      padding: rem(6px 0);
                      box-sizing: border-box;
                    }
                    button {
                      width: 100%;
                      height: rem(36px);
                      font-size: rem(14px);
                      font-weight: 700;
                      text-indent: rem(20px);
                      text-align: left;
                      letter-spacing: rem(-0.2px);
                      color: #222;
                      &:hover,
                      &.active {
                        background: #f4f7fd;
                      }
                    }
                  }
                  &:last-of-type {
                    border-left: rem(1px) solid #d6deed;
                    .select-area {
                      width: 100%;
                      padding: rem(6px 10px 6px 0);
                      box-sizing: border-box;
                      .scrolling {
                        padding: rem(0 16px 0 0);
                      }
                    }
                    button {
                      height: rem(36px);
                      font-size: rem(14px);
                      text-indent: rem(20px);
                      text-align: left;
                      letter-spacing: rem(-0.2px);
                      color: #222;
                      background: url(../img/icon-arrow-16-px.svg) no-repeat
                        right rem(10px);
                      width: rem(330px);
                      padding-right: rem(30px);
                      box-sizing: border-box;
                      text-overflow: ellipsis;
                      white-space: nowrap;
                      word-wrap: normal;
                      overflow: hidden;
                      span {
                        margin: rem(0 0 0 4px);
                        font-size: rem(12px);
                        font-weight: 700;
                        letter-spacing: rem(-0.17px);
                        color: #222;
                      }
                    }
                  }
                  .scrolling {
                    max-height: rem(370px);
                    overflow-x: hidden;
                    overflow-y: auto;
                  }
                }
              }
            }
          }
          .change-channel-of-interest {
            position: absolute;
            right: rem(51px);
            top: rem(12px);
          }
          .ui.close {
            position: absolute;
            right: rem(14px);
            top: rem(12px);
            i {
              vertical-align: top;
            }
          }
        }
      }
      .g-menu {
        .nav {
          position: absolute;
          top: rem(28px);
          left: rem(305px);
          padding-left: rem(8px);
          a {
            position: relative;
            display: inline-block;
            height: rem(20px);
            vertical-align: top;
            margin-right: rem(28px);
            font-size: rem(14px);
            font-weight: 700;
            line-height: rem(20px);
            color: #4c4c4c;
            &.active {
              color: #ff664d;
              &::before {
                content: "";
                position: absolute;
                left: rem(-8px);
                top: rem(9px);
                display: inline-block;
                width: rem(4px);
                height: rem(4px);
                background: #ff664d;
                border-radius: 100%;
                vertical-align: top;
              }
            }
          }
        }
      }
      .g-search {
        position: absolute;
        top: rem(19px);
        left: rem(752px);
        width: rem(248px);
        height: rem(38px);
        .search {
          width: 100%;
        }
      }
      .g-info {
        position: absolute;
        right: 0;
        top: 0;
        width: rem(186px);
        height: rem(100%);
        border-left: rem(1px) solid #eee;
        .balloon-pop {
          display: none;
          position: absolute;
          right: -2px;
          bottom: rem(-87px);
          width: rem(140px);
          height: rem(107px);
          background: url("../img/GNB_menu_logout.png") 0 0 no-repeat;
          background-size: 100% 100%;
          z-index: 100;
          ul {
            padding: rem(26px 20px 0);
            li {
              margin-bottom: 12px;
              a,
              button {
                display: block;
                span {
                  display: inline-block;
                  margin-left: rem(4px);
                  line-height: 22px;
                  vertical-align: top;
                  font-size: rem(14px);
                  color: #222;
                  height: 24px;
                }
              }
            }
          }
          &.show {
            display: block;
          }
        }
      }
    }
    .group-search {
      position: relative;
      height: rem(76px);
      background: #4362c5;
      .cont-inner {
        height: 100%;
      }
      .g-logo {
        position: absolute;
        padding-top: rem(26px);
      }
      .g-search {
        position: absolute;
        top: rem(14px);
        left: rem(375px);
        width: rem(450px);
        .ui.input {
          width: 100%;
        }
      }
    }

    .breadcrumbs {
      height: rem(33px);
      background: #e4e9f3;
      border-top: rem(1px) solid #d6deed;
      border-bottom: rem(1px) solid #d6deed;
      box-sizing: border-box;
      .cont-inner {
        .ui.breadcrubs {
          line-height: rem(33px);
        }
        .right {
          position: absolute;
          right: 0;
          top: 0;
          span {
            font-size: rem(12px);
            color: #222220;
            text-decoration: underline;
            margin: rem(0 3px 0 1px);
            line-height: rem(33px);
          }
          i {
            vertical-align: middle;
            margin-right: 0;
          }
          .help-desk {
            margin-top: rem(-2px);
            margin-left: rem(16px);
            display: inline-block;
            font-size: rem(12px);
            color: #222220;
            vertical-align: middle;
            line-height: rem(33px);
            i {
              margin-top: rem(-1px);
              margin-right: rem(4px);
              vertical-align: middle;
            }
          }
          .btn-logout {
            margin-left: rem(18px);
          }
        }
      }
    }

