  }
  > .content {
    box-sizing: border-box;
    min-height: calc(100vh - 76px - 33px - 90px);
    padding-bottom: rem(135px);
    > .tab-menu {
      width: 100%;
      border-top: rem(1px) solid #eee;
      border-bottom: rem(1px) solid #d6deed;
      background: #fff;
      &.fixed {
        box-shadow: -2px 5px 20px -6px rgba(38, 39, 69, 0.5);
      }
    }
    > .tab-menu2 {
      width: 100%;
      border-bottom: rem(1px) solid #d6deed;
      background: #fff;
      &.fixed {
        box-shadow: -2px 5px 20px -6px rgba(38, 39, 69, 0.5);
      }
    }
    .tab-menu-inner {
      width: rem(1200px);
      margin: rem(31px auto 0 auto);
      background: #fff;
    }
    > .summary {
      position: relative;
      top: rem(-48px);
      width: 100%;
      background: #fff;
      height: 0;
      overflow: hidden;
      .label {
        margin-right: rem(12px);
      }
      .detail-tit {
        font-size: rem(14px);
        line-height: 2;
        letter-spacing: rem(-0.35px);
        color: #4c4c4c;
        font-weight: 700;
      }
      &.fixed {
        display: block;
        top: 0;
        height: rem(48px);
        box-sizing: border-box;
        padding-top: rem(10px);
        margin-bottom: 0;
        z-index: 11 !important;
      }
    }
    &.no-padding {
      padding-bottom: 0;
    }
  }
  > .quick {
    position: fixed;
    width: 100%;
    height: 0;
    bottom: rem(100px);
    z-index: 500;

    .q-group {
      position: absolute;
      right: rem(-60px);
      bottom: 0;

      .quick {
        vertical-align: top;
        position: relative;
        z-index: 3;
      }
      .active + .quick-menu {
        display: block;
      }
      .quick-menu {
        display: none;
        position: absolute;
        right: 0;
        bottom: 0;
        width: rem(314px);
        overflow: hidden;
        z-index: 2;
        border-radius: rem(4px);
        box-shadow: rem(0 5px 14px 0) rgba(68, 68, 68, 0.8) !important;
      }
    }
    @media (max-width: 1340px) {
      .q-group {
        right: 0;
      }
      .cont-inner {
        width: auto;
      }
    }
  }
  > .footer {
    height: rem(90px);
    background: #4b4e53;
    .s-kuniversity {
      position: absolute;
      left: 0;
      top: rem(39px);
      margin: 0;
    }
    .f-copyright {
      position: absolute;
      top: rem(30px);
      left: rem(55px);
      font-size: rem(11px);
      line-height: 2.82;
      color: #999;
    }
    .f-nav {
      position: absolute;
      right: rem(0);
      // right:rem(110px);
      top: rem(35px);
      font-size: rem(14px);
      line-height: 1.43;
      a {
        color: #eee;
        &.active {
          font-weight: 700;
        }
        &::before {
          content: "";
          display: inline-block;
          vertical-align: middle;
          margin: rem(0 12px);
          width: rem(1px);
          height: rem(6px);
          opacity: 0.34;
          background-color: #b9b9b9;
        }
        &:first-of-type {
          &::before {
            display: none;
          }
        }
      }
    }
    .country {
      position: absolute;
      right: 0;
      top: rem(31px);
    }
  }
  > .center-content {
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    .align {
      max-width: 50%;
      text-align: center;
      &.loading {
        margin-top: rem(-43px);
        .loading-text {
          margin-top: rem(30px);
          font-size: rem(20px);
          color: #4c4c4c;
          letter-spacing: rem(-0.4px);
          strong {
            font-weight: 700;
          }
        }
      }
      &.error404 {
        .not-found-text {
          margin: rem(20px 0 60px);
          font-size: rem(24px);
          color: #6c788f;
          font-weight: 700;
          text-align: center;
        }
        .back {
          width: rem(158px);
          height: rem(48px);
          font-size: rem(14px);
        }
      }
    }
  }
}

/* scroll */
.scrolling-40vh {
  max-height: 40vh;
  overflow-y: auto;
}
.scrolling-60vh {
  position: static;
  max-height: 60vh;
  overflow-y: auto;
}
/* 태블릿의 경우 내부 영역이 좁아서 80%로는 충분하지 않아 70%로 수정 */
.scrolling-80vh {
  max-height: 70vh;
  overflow-y: auto;
}

